/*Landing*/




.container{
  display: flex;
  justify-content: center;
  align-items: center;
  width:100%;
  z-index:-10;
  height:80vh;
  background-image: url(/huitre-en-ligne%20/static/media/photohuitre.bf6a1d6a.jpg);
  background-size: 100%;
  background-repeat: no-repeat;
background-size: cover;
  
 
  -webkit-animation:imgChange 10s infinite;
  
 
          animation:imgChange 10s infinite;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  
  
  font-family: 'Artifika', serif;
  color:white;

}



@-webkit-keyframes imgChange{
30%{
  background-image:url(/huitre-en-ligne%20/static/media/mer4.14baf11f.jpg);
  background-size:cover;
 

}
  60%{
      background-image:url(/huitre-en-ligne%20/static/media/mer3.9da1be59.jpg);
      background-size:cover;
     
     
      
  }
  80%{
      background-image:url(/huitre-en-ligne%20/static/media/mer3.61f7462f.webp);
      background-size:cover;
     
     
     
  }
}



@keyframes imgChange{
30%{
  background-image:url(/huitre-en-ligne%20/static/media/mer4.14baf11f.jpg);
  background-size:cover;
 

}
  60%{
      background-image:url(/huitre-en-ligne%20/static/media/mer3.9da1be59.jpg);
      background-size:cover;
     
     
      
  }
  80%{
      background-image:url(/huitre-en-ligne%20/static/media/mer3.61f7462f.webp);
      background-size:cover;
     
     
     
  }
}



