/*Nav-bar*/
.navigation{
  position: absolute;
  display:flex;
    justify-content: space-between;
  left:0;
  top: 264px;
  background-color:#0b182a;
  width:100%;
  height:100%;
  -webkit-transform: translate3d(-100%,-9px,0);
          transform: translate3d(-100%,-9px,0);
  transition:-webkit-transform 500ms ease-in;
  transition:transform 500ms ease-in;
  transition:transform 500ms ease-in, -webkit-transform 500ms ease-in;


}
.hiddenMenu{
  -webkit-transform: translate3d(-100%,-9px,0);
          transform: translate3d(-100%,-9px,0);
}

.navigation h1{
 text-align: center;
 margin-top: 56px;
    font-size: 35px;

}
.nav li{
padding:20px;
}


/*show-Menu*/

.showMenu{
  transform: translate3d(0,-9px,0);
  -webkit-transform: translate3d(0,-9px,0);
  -moz-transform: translate3d(0,-9px,0);
  -ms-transform: translate3d(0,-9px,0);
  -o-transform: translate3d(0,-9px,0);
}
 
  .burger span:nth-child(1) {
    content: "";
    position: absolute;
    height: 5px;
    width: 37px;
    background-color: rgb(178, 172, 199);
    left: 18px;
    transition: 1s ease;
    border-radius: 25%;
    transform: translate3d(59px,-22px,0) rotate(       45deg);
    top: 50px;
    -webkit-transform: translate3d(59px,-22px,0) rotate(       45deg);
    -moz-transform: translate3d(59px,-22px,0) rotate(       45deg);
    -ms-transform: translate3d(59px,-22px,0) rotate(       45deg);
    -o-transform: translate3d(59px,-22px,0) rotate(       45deg);
}
.burger span:nth-child(2) {
  content: "";
  position: absolute;
  height: 5px;
  width: 37px;
  background-color: rgb(178, 172, 199);
  left: 18px;
  transition: 1s ease;
  border-radius: 25%;
  top: 50px;
  transform: translate3d(59px,-22px,0) rotate(317deg);
  -webkit-transform: translate3d(59px,-22px,0) rotate(317deg);
  -moz-transform: translate3d(59px,-22px,0) rotate(317deg);
  -ms-transform: translate3d(59px,-22px,0) rotate(317deg);
  -o-transform: translate3d(59px,-22px,0) rotate(317deg);
}
 

